import vtag from "@/components/uni-tag/uni-tag.vue"
export default {
	components: {vtag},
	data() {
		return {
			lishiSearch:[],
			searchValue: '',
			hotSearch: ['地古牛','烧仙草','火锅'],
			ss: false,
			dataList: [],
			imgUrl: ''
		}
	},
	onShow:function(){
		this.imgUrl = this.$imgUrl
	},
	methods:{
		clearlsSearch:function(){
			this.lishiSearch = []
		},
		searchclick:function(){
			var that = this
			if(this.searchValue == ''){
				uni.showToast({
					title: '搜索不能为空',
					duration:3000
				});
			}else{
				console.log(this.searchValue)
				uni.request({
					url: this.$bashUrl+ '/index.php/home/Api/search_result/',
					method: 'POST',
					data:{
						search: this.searchValue
					},
					success:(res=>{
						console.log(res)
						if(res.data == ''){
							uni.showToast({
								title: '未入驻商家或商品',
								icon: 'none'
							});
						}else{
							that.ss = true
							that.dataList = res.data
							console.log(that.dataList)
						}
						// 清空输入框
						that.searchValue = ''
					})
				})
			}
		},
		getValue:function(index){
			var that = this
			uni.request({
				url: this.$bashUrl+ '/index.php/home/Api/search_result/',
				method: 'POST',
				data:{
					search: this.hotSearch[index]
				},
				success:(res=>{
					console.log(res)
					if(res.data == ''){
						uni.showToast({
							title: '未入驻商家或商品',
							icon: 'none'
						});
					}else{
						that.ss = true
						that.dataList = res.data
					}
					// 清空输入框
					that.searchValue = ''
				})
			})
		},
		// 商品跳转
		TogoodsDatils: function(event){
			var shopID = event.currentTarget.dataset.id;
			uni.navigateTo({
			    url: '/pages/goods-details/goods-details?id=' + shopID
			})
		 }
	}
}
